    width: 100%;
}

footer a {
    color: #f5f5f5;
}

.home-main-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    text-align: center;
}

.home-project-title {
    color: #c59d5f;
    font-family: "Great Vibes", cursive;
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
    margin: 0;
}

.home-card {
    background-color: #111010;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border-radius: 10px;
    overflow: hidden;
    height: 250px;
    width: 350px;
    max-width: 100%;
    margin: 0.5rem;
}

.home-card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.home-project-logo {
    border-radius: 5px 5px 0 0;
    height: 200px;
}

@media screen and (max-width: 280px) {
    .home-title {
        font-size: 1.2rem;
    }

    .underline {
        width: 15rem;
    }

    .home-card {
        height: 160px;
        width: 260px;
        margin: 0.2rem;
    }
}

@media screen and (min-width: 320px) {
    .home-title {
        font-size: 1.2rem;
    }

    .underline {
        width: 15rem;
        margin-top: 0.5rem;
        margin-bottom: 1rem;
    }

    .home-card {
        height: 200px;
        width: 300px;
        margin: 0.2rem;
    }
}

@media screen and (min-width: 540px) {
    .home-title {
        font-size: 2.5rem;
    }

    .underline {
        width: 28rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
    }

    .home-card {
        height: 250px;
        width: 350px;
        max-width: 100%;
        margin: 0.5rem;
    }
}

